import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import users from './api/users.json'

export const LoginForm = ({ onLogin, isLogin }) => {
  const [emailUser, setEmailUser] = useState('');
  const [userPassword, setUserPassword] = useState('');
  const [error, setError] = useState(false);

  const validData = () => {
    const validUser = users.filter(user => user.email === emailUser && user.password === userPassword);

    if (validUser.length > 0) {
      onLogin(true)
    } else {
      setError(true)
      onLogin(false);
    }
  };

  if (isLogin) {
    return (
      <Redirect to="/products" />
    )
  }

  return (
    <form
      className="login-form"
      onSubmit={(event) => {
        event.preventDefault();
        validData();
      }}
    >
      <div className="login-form__text--example">
        Тестовый логин test@email.com и пароль testpassword
      </div>

      <div className="login-form__text">
        Логин
      </div>
      <input
        type="email"
        className="login-form__field field-email"
        value={emailUser}
        onChange={event => {
          setEmailUser(event.target.value);
          setError(false);
        }}
        placeholder="Введите Email"
        required
      />

      <div className="login-form__text">Пароль</div>
      <input
        type="password"
        className="login-form__field field-password"
        value={userPassword}
        onChange={event => {
          setError(false);
          setUserPassword(event.target.value)
        }}
        placeholder="Введите пароль"
        required
      />

      <button
        className="button-login"
        type="submit"
      >
        Войти
      </button>

      {error && (
        <div className="error">Введите корректный email и пароль</div>
      )}

    </form>
  )
};
