import cards from './api/data.json';
import { Link } from 'react-router-dom';
import { useState } from 'react';

import { CardItem } from './CardItem';

export const ProductCards = () => {
  const [itemId, setItemId] = useState(0);

  return (
    <div className="products__card card">
      {cards.map(card => !itemId
        ? (
          <Link
            to="/cardItem"
            className="card__item"
            key={card.CPT}
          >
            <div className="card__img"
            >
              <img className="card__img--size" src={card.ImageUrl} alt={card.Description}></img>
            </div>
            <div className="card__text text-card">
              <div className="text-card__title">
                {card.SKU}
              </div>
              <div className="text-card__price">
                {card['Retail Price']} грн
              </div>
            </div>
          </Link>
        ) : (
          <CardItem itemId={itemId} />
        )
      )}
    </div>
  )
};
