import { useEffect, useState } from "react";
import { Redirect } from "react-router";
import data from './api/data.json'

export const CardItem = ({ isLogin }) => {
const [vissibleCard, setVissibleCard] = useState(62);
const card = data.filter(el => el.CPT === vissibleCard);

useEffect(() => {
  setVissibleCard(62);
}, [])


  if (!isLogin) {
    return (
      <Redirect to="/login-form" />
    )
  }

  return (
    <>
    {card[0].Description}
    </>
  )
};
